.header {
  text-align: center;
  padding: 50px;
  background: linear-gradient(135deg, #6a11cb, #2575fc); /* Modern gradient with cool tones */
  color: #f9f9f9;
  border-radius: 15px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
}

.photo {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 4px solid #f9f9f9;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.photo:hover {
  transform: scale(1.1);
}

h1 {
  font-size: 3rem;
  margin-bottom: 15px;
  letter-spacing: 1px;
}

h1 .highlight {
  color: #ffde59; /* Subtle yellow for highlighting */
  font-weight: bold;
}

h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  font-style: italic;
  color: #e2e8f0; /* Soft neutral for secondary text */
}

.description {
  font-size: 1.3rem;
  margin-bottom: 30px;
  font-weight: 400;
  color: #d1d5db; /* Light neutral tone */
}

.social-links a {
  margin: 0 10px;
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
  border: 2px solid transparent;
  padding: 10px 20px;
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.social-links a:hover {
  background: #ffffff;
  color: #2575fc; /* Matches gradient tone */
  border-color: #2575fc;
  transform: translateY(-2px);
}
